<script>
  const Gn = window.gnui

  export default {
    name: 'GnUI',
    render: function (createElement) {
      return createElement(
        'div',
        {
          ref: 'gnui'
        },
        this.$slots.default
      )
    },
    props: {
      name: {
        type: String,
        default: 'gnui'
      },
      uiType: {
        type: String,
        required: true
      },
      options: {
        type: Object
      },
      value: {
        type: String
      }
    },
    mounted() {
      this.comp = Gn.create(this.uiType, this.createOptions)
    },
    computed: {
      createOptions(){
        if(this.value){
          return Gn.util.objExtends({ value: this.value }, this.options, { target: this.$refs.gnui })
        }
        return Gn.util.extend(this.options, { target: this.$refs.gnui })
      }
    },
    data() {
      return {
        comp: undefined
      }
    },
    beforeDestroy() {
      if (this.comp) {
        this.comp.$destroy()
      }
    }
  }
</script>
